import {  Component,  Vue} from 'vue-property-decorator';
import {  CharactersDto } from '@/models/CharactersDto.ts';
import {  charactersService } from '@/services/characters-service.ts';
import {  AxiosResponse } from 'axios';

@Component({
  components: {}
})
export default class ListCharacters extends Vue {
  private characterDto: CharactersDto = new CharactersDto();
  private listCharacter: any[] = [];
  private mounted() {
    this.getListCharacters();
  }

  private getListCharacters() {
    charactersService.getAllCharacters()
    .then((res: any) => {
      this.listCharacter = res.data;
    })
    .catch((err: any) => {
      console.log(err);
    })
  }


}
